.main-game-container {
  font-size: 16px;
  background-color: #414141;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.main-game-container .title {
  background-color: initial;
  border: none;
  color: white;
  font-size: 36px;
  cursor: pointer;
  user-select: none;
  z-index: 100;
}

.main-game-container .finish-text {
  position: absolute;
  bottom: 5%;
  color: white;
  font-size: 24px;
  z-index: 50;
  user-select: none;
}

.zone-container {
  box-sizing: border-box;
  /*outline: 4px solid white;*/
  margin: 0.5rem auto;
  /*width: 1000px;*/
  /*height: 700px;*/
  /*96*9*/
